* {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.all-content {
  max-width: 900px;
  width: 100%;
  /* height: 100%; */
  border: 1px solid rgba(211, 210, 210, 0.9);
  position: absolute;
  top: 0;
  /* overflow: auto; */
}

.title-area {
  width: calc(100% - 0px);
  text-align: center;
  background-color: rgb(7, 68, 121);
  color: white;
  padding: 15px;
  font-size: 18px;
}

form input, form select, form textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  resize: vertical;
}

.input-search {
  width: 170px;
}

input:focus, select:focus, textarea:focus {
  border: 1px solid rgba(61, 120, 247, 0.9);
}


label {
  padding: 12px 12px 12px 0;
  display: inline-block;
}

input[type=submit] {
  background-color: #4CAF50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: right;
}

input[type=submit]:hover {
  background-color: #45a049;
}

.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.col-25 {
  float: left;
  width: 25%;
  margin-top: 6px;
}

.col-75 {
  float: left;
  width: 75%;
  margin-top: 6px;
}

.row {
  padding-left: 15px;
  padding-right: 15px;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

.btn-save {
  background-color: rgb(61, 120, 247);
  color: white;
  padding: 10px 20px;

  width: calc(100% - 30px);
  font-size: 1em;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
  /* margin-bottom: 40px; */
}

.btn-back-page {
  text-align: center;
  text-decoration: none;
  background-color: rgb(194, 77, 77);
  color: white;
  padding: 10px 20px;

  width: calc(100% - 30px);
  font-size: 1em;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn-view-report {
  text-align: center;
  text-decoration: none;
  background-color: rgb(29, 159, 163);
  color: white;
  padding: 10px 20px;

  width: calc(100% - 30px);
  font-size: 1em;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  border-radius: 4px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn-view-report:hover {
  background-color: rgba(29, 159, 163, 0.9);
}

.btn-save:hover {
  background-color: rgba(61, 120, 247, 0.9);
}

.btn-back-page:hover {
  background-color: rgba(194, 77, 77, 0.9);
}

form {
  margin-top: 25px;
}

.form-btn {
  margin-top: 35px;
  margin-bottom: 35px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.back-page {
  margin-top: 15px;
  margin-bottom: 35px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.view-report {
  margin-top: 15px;
  margin-bottom: 15px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}


textarea, input {
  resize: none;
}

.main-app {
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 height: calc(100vh - 0px);
}

/* Responsive layout - when the screen is less than 600px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
  .col-25, .col-75, input[type=submit] {
    width: 100%;
    margin-top: 0;
  }
}

.table-container {
  /* padding: 20px; */
  overflow: auto;
  width: calc(100% - 20px);
}

.styled-table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.styled-table thead tr {
  background-color: #009879;
  color: #ffffff;
  text-align: left;
}

.styled-table th,
.styled-table td {
    padding: 12px 15px;
}
.styled-table tbody tr {
  border-bottom: 1px solid #dddddd;
}

.styled-table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

.styled-table tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}
.styled-table tbody tr.active-row {
  font-weight: bold;
  color: #009879;
}

.codigo {
  width: 80px;
}

.table-custom-content tr {
  cursor: pointer;
}

.actions-table {
  display: flex;
  flex-wrap: wrap;
  margin-top: 25px;
}

.add-color {
  background-color: rgb(57, 76, 163) !important;
  color: white !important;
}

.cell-selected {
  background-color: #4f90f3 !important;
  color: white !important;
}

.delete-color {
  background-color: rgba(194, 57, 57, 0.9) !important;
  color: white !important; 
}

.sidebar {
  position: fixed;
  top: 66px;
  left: 0;
  background-color: rgb(248, 248, 248);
  border: 1px solid rgba(0, 0, 0, 0.15);
  height: calc(100vh - 66px);
  padding: 15px;
  width: 300px;
  overflow: auto;
  z-index: 999;
}

.sidebar-head {
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(19, 19, 19, 0.377);
}

.sidebar-head .title-section, .indicator-help-footer {
  font-size: 18px;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: gray;
}

.sidebar-body .section-content  {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
}

.dropdown-section {
  margin-top: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 0px;
  padding: 0;
  display: none;
}

.d-flex-show {
  display: flex !important;
}


.sidebar-body .item-section, .content-help-footer {
  margin: 0;
  padding-top: 10px;
  padding-bottom: 10px;
  text-decoration: none;
  color: rgb(61, 133, 214);
  /* background-color: rgba(255, 255, 255, 0.404); */
  padding: 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
}

.sidebar-body .dropdown-section {
  margin: 0;
  /* padding-top: 10px; */
  /* padding-bottom: 10px; */
  padding-left: 15px;
  text-decoration: none;
  color: rgb(61, 133, 214);
  /* background-color: rgba(255, 255, 255, 0.404); */
  /* padding: 10px; */
  border-bottom: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
}

.sidebar-body .item-section i:nth-child(3) {
  float: right;
  margin-top: 3px;
}

.sidebar-body .item-section i:nth-child(1) {
  margin-right: 10px;
}

[data-target-section="entradas"] i:nth-child(1) {
  margin-right: 14px !important;
  margin-left: 4px;
}

[data-target-section="salidas"] i:nth-child(1) {
  margin-right: 11px !important;
  margin-left: -2px;
}

.sidebar-body .item-section:hover, .content-help-footer:hover {
  background-color: rgb(61, 133, 214);
  color: rgb(238, 238, 238);
}

.selected-option-sidebar {
  background-color: rgba(0, 0, 0, 0.09) !important;
  color: rgb(46, 91, 175) !important;
}

.sidebar-user-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 25px;
}

.sidebar-user-info .name-user {
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 18px;
  font-family: 'Times New Roman', Times, serif;
  color: black;
  font-weight: bold;
  color: #585858;
}

.sidebar-user-info .img-user {
  width: 80px;
  height: 80px;
}

.sidebar-user-info .img-user img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.indicator-help-footer {
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(19, 19, 19, 0.377);
  margin-bottom: 25px;
}

.navbar {
  position: fixed;
  top: 0;
  /* left: 300px; */
